export function updateUserProfile(data) {
    return dispatch => {
        dispatch(_updateProfile(data));

        return profileAPI.updateProfile(data)
            .then(res => {
                dispatch(updateProfileSuccess(res.data))
            })
            .catch(err => {
                dispatch(updateProfileError({
                    error: err.response
                }))
            })
    }
}
